* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  height: 100vh;
  background-color: #b9bcb7;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

ul, ol {
  margin: 0;
  padding-left: 0;
}

button {
  cursor: pointer;
}

img {
  width: 100%;
  height: auto;
  max-width: 100%;
  display: block;
}

.list {
  list-style: none;
}

.link {
  font-size: 12px;
  text-decoration: none;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
  position: absolute;
}

.search-form {
  z-index: 3;
  position: absolute;
}

.gallery {
  height: 100%;
  width: 100%;
  grid-template-columns: repeat(auto-fill, minmax(30%, 1fr));
  grid-auto-rows: 20vh;
  gap: .2rem;
  margin-top: 10vh;
  margin-bottom: 100px;
  display: grid;
  position: relative;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.photo-card {
  height: 100%;
  background-color: #7fffd4;
}

.decorWaves {
  z-index: 2;
  position: absolute;
  transform: rotate(180deg);
}

.waves {
  width: 100%;
  height: 30vh;
  min-height: 5vh;
  max-height: 15vh;
  margin-bottom: -7px;
  position: relative;
}

@media (max-width: 768px) {
  .waves {
    height: 10vh;
  }
}

.parallax > use {
  animation: move-forever 25s cubic-bezier(.55, .5, .45, .5) infinite;
}

.parallax > use:nth-child(1) {
  animation-duration: 7s;
  animation-delay: -2s;
}

.parallax > use:nth-child(2) {
  animation-duration: 10s;
  animation-delay: -3s;
}

.parallax > use:nth-child(3) {
  animation-duration: 13s;
  animation-delay: -4s;
}

.parallax > use:nth-child(4) {
  animation-duration: 20s;
  animation-delay: -5s;
}

@keyframes move-forever {
  0% {
    transform: translate3d(-90px, 0, 0);
  }

  100% {
    transform: translate3d(85px, 0, 0);
  }
}

